//存放对象数据
const state = {
    user:{
        name:"hello"
    }
}
//获取对象数据，计算属性
const getters = {
    getUser(state){
        return state.user;
    }
}
//唯一修改数据方法
const mutations = {
    setUser(state,user){
        state.user = user;
    }
}
//使用此方法可以异步调用修改方法mutations
const actions = {
    asyncSetUser(context,user){
        context.commit('setUser',user);
    }
}
//最后暴露出去
export default{
    namespaced: true,
    state,
    getters,
    mutations,
    actions

}