<script>
import config from "@/config/config.js";
// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from "swiper/vue";
// Import Swiper styles
import "swiper/css";
import { reactive } from "vue-demi";
import { width } from "dom7";

export default {
  name: "newPage",
  methods: {
    toHomepage() {
      this.$router.push("/");
    },
  },
  computed: {
    goods() {
      return config.goods;
    },
  },
  data() {
    return {};
  },
  methods: {
    change(item) {
      //跳转并携带值
      this.$router.push({ path: "goodsPage", query: { name: item.name } });
    },
  },
  mounted() {},
  components: {
    Swiper,
    SwiperSlide,
  },
  setup() {
    const onSwiper = (swiper) => {
      // console.log(swiper);
    };
    const onSlideChange = () => {
      console.log("slide change");
    };
    //轮播图设置参数
    //reactive的用法与ref的用法相似，也是将数据变成响应式数据，当数据发生变化时UI也会自动更新。
    //不同的是ref用于基本数据类型，而reactive是用于复杂数据类型，比如对象和数组
    const swiper_options = reactive({
      navigation: {
        nextElRef: ".swiper-button-next",
        prevElRef: ".swiper-button-prev",
      },
    });
    return {
      onSwiper,
      onSlideChange,
      swiper_options,
    };
  },
};
</script>

