<template>
  <div class="container">
    <div class="header">
      <div class="search-box" @click="toSearch">
        <i class="fa-search"></i>&nbsp;&nbsp;
        <span>搜索</span>
        <el-button color="#eb5ab6" class="search" :icon="Search" />
      </div>
      <i class="icon-qrcode"></i>
    </div>
    <div class="content">
      <h2>{{ news.title }}</h2>
      <p class="news-profile">{{ news.source }}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{ news.time }}</p>
      <div v-html="news.content"></div>
      <ul>
        <li class="keyword-item"><i class="fa fa-key"></i></li>
        <li
          v-for="(keyword, index) in news.keywords"
          :key="index"
          class="keyword-item">
          {{ keyword }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script setup>
import { Search } from "@element-plus/icons-vue";
</script>
<script>

  import { mapGetters, mapActions } from 'vuex'
  export default {
    name: 'NewsPage',
    mounted () {
      this.$nextTick(function () {
        this.getNews({
          id: this.$route.query.id
        })
      })
    },
    computed: {
      ...mapGetters('News', ['news'])
    },
    methods: {
      ...mapActions('News', ['getNews']),
      toMain () {
        this.$router.push('/main')
      },
      toSearch () {
        this.$router.push('/search')
      }
    }
  }
</script>

<style scoped>
  .container {
    height: 100%;
  }
  .header {
  padding: 8px 15px;
  background-color: #3d80d3;
  overflow: hidden;
}
.search-box {
  display: inline-block;
  vertical-align: middle;
  width: 90%;
  line-height: 32px;
  background-color: #fff;
  border-radius: 5px;
  padding-left: 10px;
}
.search-box > i {
  color: #707070;
}
  .icon-bg {
    font-size: 20px;
  }
  .pull-right {
    float: right;
  }
  .content {
    box-sizing: border-box;
    padding: 5px 15px;
    height: calc(100% - 35px);
    overflow-y: auto;
    color: #222;
  }
  .news-profile {
    padding-top: 6px;
    padding-bottom: 10px;
    color: #999;
  }
  .keyword-item {
    margin-right: 10px;
    list-style: none;
    display: inline-block;
    color: #4095d8;
  }
  .keyword-item:first-child {
    font-size: 16px;
    margin-right: 5px;
  }
  .search {
  float: right;
}
</style>
