<template>
  <div class="home" style="text-align: center">
    <!-- 判断如果有数据进行加载 否则销毁  -->
    <div class="天气预报" v-if="days[0]">
      <header>
        <el-input
          style="display: inline-block"
          class="search"
          v-model="input"
          placeholder="请输入内容"
        ></el-input>
        <el-button
          style="display: inline-block"
          type="primary"
          :icon="Search"
          @click="search()"
          >搜索</el-button
        >
      </header>
      <div class="cityName">
        <span id="citynameGn">{{ resolvedAddress }}</span>
        <i id="Switch">【切换】</i>
      </div>
      <div class="skTempWea">
        <h1 id="wendu" style="font-size: 90px">{{ days[0].temp }}°</h1>
        <div class="skWea">
          <i><img id="tubiao" /></i>
          <p id="tianqi">{{ days[0].conditions }}</p>
          <div class="day">
            <p id="desc">{{ days[0].description }}</p>
            <p style="display: inline-block" id="riqi">
              日期:{{ days[0].datetime }}
            </p>
            &nbsp;&nbsp;
            <p style="display: inline-block" id="riqi">
              日出时间:{{ days[0].sunrise }}
            </p>
            &nbsp;&nbsp;
            <p style="display: inline-block" id="riqi">
              日落时间:{{ days[0].sunset }}
            </p>
            &nbsp;&nbsp;
          </div>
          <div class="tablebox">
            <Table class="table" :days="days" :addr="resolvedAddress"></Table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
// @ is an alias to /src
import Table from "@/components/Table.vue";
import Hello from "@/components/HelloWorld.vue";
export default {
  name: "HomeView",
  data() {
    return {
      resolvedAddress: "",
      address: "",
      latitude: "",
      longitude: "",
      days: [],
      cloudcover: "",
      conditions: "",
      datetime: "",
      description: "",
      input: "",
      city: "yangjiang",
    };
  },
  components: {
    Table,
  },
  mounted() {},
  // 页面加载就调用
  created() {
    this.requestData();
  },
  methods: {
    //调用API获取数据
    requestData() {
      this.axios
        .get(
          "https://weather.visualcrossing.com/VisualCrossingWebServices/rest/services/timeline/" +
            this.city +
            "?unitGroup=metric&key=6UH4AP97G2LV8ZKWFS95TLK46&contentType=json"
        )
        .then((res) => {
          this.resolvedAddress = res.data.resolvedAddress;
          this.address = res.data.address;
          this.latitude = res.data.latitude;
          this.longitude = res.data.longitude;
          this.days = res.data.days;
          console.log(res.data.days[0]);
        });
    },
    // 搜索框点击再次调用API
    search() {
      this.city = this.input;
      this.requestData();
    },
  },
};
</script>

