//存放对象数据
const state = {
        Categories:[]
}
//获取对象数据，计算属性
const getters = {
    getCategories(state){
        return state.Categories;
    }
}
//唯一修改数据方法
const mutations = {
    setCategories(state,Categories){
        state.Categories = Categories;
    }
}
//使用此方法可以异步调用修改方法mutations
const actions = {
    //context是上下文的一丝
    asyncSetCategories(context,Categories){
        context.commit('setCategories',Categories);
    }
}
//最后暴露出去
export default{
    namespaced: true,
    state,
    getters,
    mutations,
    actions

}