<template>
    <div>
        <h1> {{ fontSize }}</h1>
        <h1> {{ color }}</h1>
        <h1> {{ text }}</h1>
       
    </div>
</template>
<script>
  export default {
    name: 'Badge',
    props: {
        name:{
            default: "default"
        }
        ,
      color: {
        default: '#d33d3e',
        //检测到输入的内容不符合要求就弹出
        validator (value) {
          return value.indexOf('#') === 0 && (value.length === 4 || value.length === 7)
        }
      },
      text: {
        default: 'text',
        validator(value) {
          console.log(typeof value === 'string')
          if(!(typeof value === 'string')){
            throw new Error('!!!');
          }
      }
      },
      fontSize: {
        default: '12px'
      }
    },mounted(){
       
    }
  } 
</script>