<template>
    <el-table
      :data="days"
      style="width: 100%"
      :row-class-name="tableRowClassName">
      <el-table-column
        prop="datetime"
        label="日期"
        width="180">
      </el-table-column>
      <el-table-column
        prop="conditions"
        label="天气"
        width="180">
      </el-table-column>
      <el-table-column
        prop="temp"
        label="温度"
        width="180">
      </el-table-column>
      <el-table-column
        prop="sunrise"
        label="日出时间"
        width="180">
      </el-table-column>
      <el-table-column
        prop="sunset"
        label="日落时间"
        width="180">
      </el-table-column>
      <el-table-column
        prop="addr"
        label="位置">
        {{ addr }}
      </el-table-column>
    </el-table>
  
  </template>
  

  <style>
    .el-table .warning-row {
      background: oldlace;
    }
  
    .el-table .success-row {
      background: #f0f9eb;
    }
  </style>
  
  <script>
    export default {
      methods: {
        tableRowClassName({row, rowIndex}) {
          if (rowIndex === 1) {
            return 'warning-row';
          } else if (rowIndex === 3) {
            return 'success-row';
          }
          return '';
        }
      },
     
      data() {
        return {
          tableData: []
        }
      },
      //父传子
      props:["days","addr"],
      mounted(){
        console.log(this.days[0])
    }
    }
   
    
  </script>